import firebaseDB from '@/firebase-config'
import { doc, collection, getDocs, addDoc, deleteDoc, updateDoc, query, where } from 'firebase/firestore/lite'

class DbOperations {
    constructor(collectionTitle) {
        this.dbCollection = collection(firebaseDB, `/${collectionTitle}`)
    }
    getListFromSnapshot(snapshot) {
        const list = []
        snapshot.docs.forEach((doc) => {
            list.push({
                id: doc.id,
                ...doc.data(),
            })
        })
        return list
    }
    loadItemsList() {
        return new Promise((resolve, reject) => {
            getDocs(this.dbCollection)
                .then((querySnapshot) => {
                    resolve(this.getListFromSnapshot(querySnapshot))
                })
                .catch((error) => {
                    reject(error)
                })
        })
    }

    addItem(item) {
        return new Promise((resolve, reject) => {
            addDoc(this.dbCollection, item)
                .then(() => {
                    resolve(true)
                })
                .catch((error) => {
                    reject(error)
                })
        })
    }

    deleteItem(id) {
        return new Promise((resolve, reject) => {
            deleteDoc(doc(this.dbCollection, id))
                .then(() => {
                    resolve(true)
                })
                .catch((error) => {
                    reject(error)
                })
        })
    }

    updateItem(itemId, data) {
        console.log('itemId')
        console.log(itemId)
        console.log('data')
        console.log(data)
        return new Promise((resolve, reject) => {
            const oldDocRef = doc(this.dbCollection, itemId)
            updateDoc(oldDocRef, data)
                .then(() => {
                    resolve(true)
                })
                .catch((error) => {
                    reject(error)
                })
        })
    }
		loadFilteredData(filters) {
			let queryObject = this.dbCollection;
	
			filters.forEach(({ fieldTitle, compareOperator, valueToCompare }) => {
					queryObject = query(queryObject, where(fieldTitle, compareOperator, valueToCompare));
			});
	
			return new Promise((resolve, reject) => {
					getDocs(queryObject)
							.then((querySnapshot) => {
									resolve(this.getListFromSnapshot(querySnapshot));
							})
							.catch((error) => {
									reject(error);
							});
			});
	}
}
export default DbOperations








